*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
  }
  body{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background: #5256AD  no-repeat 30px 20px; 
      /* color: #fff; Цвет текста */
  }
  ::selection{
    color: rgb(255, 255, 255);
    background: #5256AD;
  }
  .wrapper{
    width: 280px;
    padding: 12px 15px;
    border-radius: 7px;
    background: #fff;
    box-shadow: 7px 7px 20px rgba(0,0,0,0.05);
  }
  .wrapper header{
    font-size: 14px;
    font-weight: 500;
    text-align: center;
  }
  .wrapper form{
    margin: 35px 0 20px;
  }
  form .row{
    display: flex;
    margin: 4px;
    flex-direction: column;
  }
  form .row label{
    font-size: 14px;
    margin-bottom: 2px;
  }
  form .button label{
    font-size: 14px;
    margin-bottom: 2px;
  }
  form .sec label{
    font-size: 14px;
    margin-bottom: 2px;
  }
  form .row:nth-child(2) label{
    font-size: 14px;
  }
  form :where(select, button){
    outline: none;
    width: 100%;
    height: 100%;
    border: none;
    border-radius: 5px;
  }
  form .row textarea{
    resize: none;
    height: 100%;
    width: 100%;
    font-size: 20px;
    padding: 8px 10px;
    border: 1px solid #999;
    text-align:center;
  }
 form .row input {
    width: 25px;
    margin-top: 20px;
    text-align:center;
}
  form .row textarea::-webkit-scrollbar{
    width: 0px;
  }
  form .row .outer{
    height: 25px;
    display: flex;
    padding: 0 10px;
    align-items: center;
    border-radius: 5px;
    justify-content: center;
    border: 1px solid #999;
  }
  form .row select{
    font-size: 13px;
    background: none;
  }
  form .row select::-webkit-scrollbar{
    width: 10px;
  }
  form .row select::-webkit-scrollbar-track{
    background: #fff;
  }
  form .row select::-webkit-scrollbar-thumb{
    background: #888;
    border-radius: 8px;
    border-right: 2px solid #ffffff;
  }
  form button{
    width: 49%; 
    height: 42px;
    color: #fff;
    font-size: 14px;
    cursor: pointer;
    margin-top: 10px;
    background: #675AFE;
    transition: 0.3s ease;
    border: 2px solid #f3eeee;
  }
  form button:hover{
    background: #4534fe;
  }
  form .row button {
    width: 100%;
    height: 42px;
    margin-top: 0px;
    /* border: 2px solid #f3eeee; */
  }
  
  @media(max-width: 400px){
    .wrapper{
      max-width: 345px;
      width: 100%;
    }
  }